import { Vue, Component, Prop, Watch } from "vue-property-decorator";

import Url from "../../../api";

@Component({})
export default class RoleManagementDetail extends Vue {
  @Prop({ type: Boolean, default: false })
  value!: boolean;

  @Prop({ type: Object, default: {} })
  data!: any;

  @Watch("value")
  newValue(val: boolean) {
    this.addDialog = val;
    if (val) {
      this.$nextTick(() => {
        const form: any = this.$refs.form;
        form.resetFields();
        this.form = JSON.parse(JSON.stringify(this.data));
        this.form.remark = this.data.remark ? this.data.remark : "";
      });
    }
  }

  addDialog = false;
  form: any = {};
  formRules: any = {
    name: {
      required: true,
      message: "该选项为必填项",
      trigger: "blur"
    },
    code: {
      required: true,
      message: "该选项为必填项",
      trigger: "blur"
    },
    defaultRole: {
      required: true,
      message: "该选项为必填项",
      trigger: "blur"
    }
  };
  loading: boolean = false;
  created(): void {}

  handleSave() {
    const form: any = this.$refs.form;
    form.validate((flag: boolean) => {
      if (flag) {
        this.saveForm();
      }
    });
  }

  async saveForm() {
    this.loading = true;
    if (typeof this.form.remark === "string") {
      this.form.remark = this.form.remark.trim();
    }
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(this.form.id ? Url.updateRole : Url.addRole, this.form);
      if (status) {
        this.$message.success(this.form.id ? "修改成功" : "添加成功");
        this.handleBack(true);
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleBack(flag: boolean) {
    this.addDialog = false;
    this.$emit("complete", flag);
  }
}
