


























/**
 * @Description: 角色权限配置
 * @Author: liuyang
 * @Date: 2019-06-28 9:27
 */
import { Vue, Component, Prop } from "vue-property-decorator";

import Url from "../../../api";

@Component({})
export default class RoleManagementNav extends Vue {
  @Prop({ type: Object, required: true })
  data!: any;

  loading: boolean = false;
  allOperationList: Array<any> = [];
  navIdList: Array<any> = [];
  roleOperationMap: any = {};
  defaultProps = {
    children: "children",
    label: "name"
  };
  styleScopedId: string | undefined = "";
  // 用于保存选中数据 id -> ids[{}]
  treeIdMap: any = {};
  disabled: boolean = this.data.roleType === "GENERAL";

  created(): void {
    this.matchOperation();
  }

  mounted() {
    let tree: any = this.$refs.roleTree;
    this.styleScopedId = Object.keys(tree.$el.dataset).find((d: string) => d.substring(0, 2) === "v-");
  }

  async matchOperation() {
    this.loading = true;
    try {
      await this.getAllOperationList();
      await this.getRoleOperationList();
    } finally {
      this.loading = false;
    }
  }

  async getRoleOperationList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(Url.getRoleOperationList + this.data.id);
    if (status) {
      this.navIdList = data.navigationIds;
      this.roleOperationMap = {};
      data.navigationOperationIds.forEach((id: string) => {
        this.roleOperationMap[id] = true;
      });
    } else {
      this.$message.error(message);
    }
  }

  // 给数据加disabled属性
  addDisalbeProp(arr: Array<any>) {
    arr.forEach((li: any) => {
      li.disabled = this.disabled;
      if (li.children && li.children.length) {
        this.addDisalbeProp(li.children);
      }
    });
  }

  async getAllOperationList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(Url.getAllOperationList);
    if (status) {
      this.addDisalbeProp(data);
      this.allOperationList = data;
    } else {
      this.$message.error(message);
    }
  }

  handleCheck(data: any, checked: boolean) {
    this.treeIdMap[data.id].forEach((li: any) => {
      li.checked = checked;
    });
  }

  renderContent(h: any, { node, data, store }: { node: any; data: any; store: any }) {
    // 给每一个节点添加当前组件的样式作用域hash值 data-v-xxxx
    const assignScopedId = (obj: any) => {
      if (this.styleScopedId) {
        obj.attrs = obj.attrs || {};
        obj.attrs["data-" + this.styleScopedId] = true;
      }
      return obj;
    };
    if (!this.treeIdMap[data.id]) {
      this.treeIdMap[data.id] = data.oparations.map((item: any) => {
        return {
          id: item.id,
          checked: this.roleOperationMap[item.id]
        };
      });
    }
    return h(
      "div",
      assignScopedId({
        class: "tree-item"
      }),
      [
        data.name,
        h(
          "div",
          assignScopedId({
            class: "right"
          }),
          data.oparations.map((item: any, index: number) => {
            return h(
              "div",
              assignScopedId({
                class: "item",
                props: {
                  key: index
                }
              }),
              [
                h("el-checkbox", {
                  style: { marginRight: "8px" },
                  props: {
                    disabled: !node.checked || this.disabled,
                    checked: this.treeIdMap[data.id][index].checked,
                    value: this.treeIdMap[data.id][index].checked
                  },
                  on: {
                    input: (val: boolean) => {
                      this.treeIdMap[data.id][index].checked = val;
                    }
                  }
                }),
                item.operationName
              ]
            );
          })
        )
      ]
    );
  }

  async handleSave() {
    let checkedNodeIdMap: any = {};
    let tree: any = this.$refs.roleTree;
    tree.getCheckedNodes().forEach((item: any) => {
      checkedNodeIdMap[item.id] = true;
    });
    let requestBody = Object.keys(this.treeIdMap)
      .filter((id: string) => checkedNodeIdMap[id])
      .map((id: any) => {
        return {
          navigationId: id,
          operationIds: this.treeIdMap[id].filter((d: any) => d.checked).map((d: any) => d.id)
        };
      });
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.updateRoleNavOperation + this.data.id, requestBody);
      if (status) {
        this.$message.success("保存成功");
        this.$emit("hidden");
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleBack() {
    this.$emit("hidden");
  }
}
