





















































































import { Vue, Component, Prop } from "vue-property-decorator";
import RoleManagementDetail from "./components/RoleManagementDetail.vue";
import RoleManagementNav from "./components/RoleManagementNav.vue";
import DataView from "./components/DataView.vue";

import Url from "../../api";

@Component({
  components: {
    RoleManagementDetail,
    RoleManagementNav,
    DataView
  }
})
export default class RoleManagement extends Vue {
  loading: boolean = false;
  tableData: Array<any> = [];
  name: string = "";
  code: string = "";
  pagination: any = {
    current: 1,
    size: 10
  };
  total: number = 0;

  roleVisible: boolean = false;
  navVisible: boolean = false;
  dataVisible: boolean = false;
  roleItem: any = {
    id: "",
    name: "",
    code: "",
    roleType: "NORMAL",
    defaultRole: false,
    remark: ""
  };

  created(): void {
    this.getTableData();
  }

  search() {
    this.pagination.current = 1;
    this.getTableData();
  }

  async getTableData() {
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.getRoleList, {
        ...this.pagination,
        query: {
          name: this.name,
          code: this.code
        }
      });
      if (status) {
        this.tableData = data.records;
        this.total = data.total;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleCurrentChange(val: number) {
    this.pagination.current = val;
    this.getTableData();
  }
  handleSizeChange(val: number) {
    this.pagination.size = val;
    this.getTableData();
  }

  // 添加角色
  handleAdd() {
    this.roleItem = {
      id: "",
      name: "",
      code: "",
      roleType: "NORMAL",
      defaultRole: false,
      remark: ""
    };
    this.roleVisible = true;
  }

  // 修改角色
  handleEditRole(item: any) {
    this.roleItem = item;
    this.roleVisible = true;
  }

  // 删除角色
  handleDelete(item: any) {
    let _this = this;
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(Url.deleteRole + "/" + item.id).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              //重新获取列表
              _this.getTableData();
            } else {
              _this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }

  handleHiddenRole(flag: boolean) {
    this.roleVisible = false;
    if (flag) {
      this.search();
    }
  }

  // 菜单权限
  handleEditNav(item: any) {
    this.navVisible = true;
    this.roleItem = item;
  }

  handleHiddenNav() {
    this.navVisible = false;
    this.search();
  }

  // 数据权限
  handleEditData(item: any) {
    this.dataVisible = true;
    this.roleItem = item;
  }

  handleHiddenData() {
    this.dataVisible = false;
    this.search();
  }
}
