




















/**
 * @Description: 角色权限配置
 * @Author: liuyang
 * @Date: 2019-06-28 9:27
 */
import { Vue, Component, Prop } from "vue-property-decorator";

import Url from "../../../api";

@Component({})
export default class RoleManagementNav extends Vue {
  @Prop({ type: Object, required: true })
  data!: any;

  loading: boolean = false;
  allOperationList: Array<any> = [];
  navIdList: Array<any> = [];
  roleOperationMap: any = {};
  defaultProps = {
    children: "children",
    label: "desc",
    id: "dataResourceType"
  };
  styleScopedId: string | undefined = "";
  // 用于保存选中数据 id -> ids[{}]
  treeIdMap: any = {};
  disabled: boolean = this.data.roleType === "GENERAL";

  created(): void {
    this.matchOperation();
  }

  async matchOperation() {
    this.loading = true;
    try {
      await this.getRoleOperationList();
      this.getAllOperationList();
    } finally {
      this.loading = false;
    }
  }

  async getAllOperationList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(Url.getAllDataList);
    if (status) {
      data.dataResourceTypeVOS.forEach((item: any) => {
        item.children = JSON.parse(JSON.stringify(data.dataScopeEnumVOS));
        if (this.roleOperationMap[item.dataResourceType]) {
          item.checked = this.roleOperationMap[item.dataResourceType];
        } else {
          item.checked = "";
        }
      });
      this.allOperationList = data.dataResourceTypeVOS;
    } else {
      this.$message.error(message);
    }
  }

  async getRoleOperationList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(Url.getSelectDataList + this.data.id);
    if (status) {
      this.roleOperationMap = {};
      data.forEach((li: any) => {
        this.roleOperationMap[li.dataResourceType] = li.dataScopeEnum;
      });
    } else {
      this.$message.error(message);
    }
  }

  async handleSave() {
    let requestBody: Array<any> = [];
    this.allOperationList.forEach((item: any) => {
      if (item.checked) {
        requestBody.push({
          dataResourceType: item.dataResourceType,
          dataScopeEnum: item.checked
        });
      }
    });
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.updateDataView + this.data.id, requestBody);
      if (status) {
        this.$message.success("保存成功");
        this.$emit("hidden");
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleBack() {
    this.$emit("hidden");
  }
}
